@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Open+Sans:ital,wght@0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=Poppins:wght@300;400;500&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}
.nav-logo {
  position: absolute;
  color: black;
  font-size: 24px;
  font-weight: 700;
  margin: 10px 10px;
  padding: 10px 10px;
}
.nav-logo a {
  text-decoration: none;
}
.nav-menu {
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
